.alphabet {
    text-align: center;
    font-size: 60px;
    padding: 0px 10px;
    background: url('../../assets/alBg.webp') no-repeat;
    background-size: cover;
    background-position: 0%;
}

.alphabet__container {
    padding-top: 20px;
}

.alphabet__main {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 15px;
}

.alphabet__button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    cursor: pointer;
    color: white;
    text-align: center;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
    border-radius: 10px;
    animation: btn__animate 8s ease-in-out infinite 1s;
}

.alphabet__button h3{
    font-size: 60px;
}

.alphabet__button p{
    font-size: 20px;
}


@keyframes btn__animate {
    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 40%;
    }
    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}